<template>
  <section class="bg-gray-200 text-primary">
    <div class="container lg:w-5/6 py-20">
      <div class="text-center uppercase font-sans font-bold">
        now hiring
      </div>
      <div class="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-10 my-10">
        <div 
          v-for="(item, i) in result" 
          :key="item.id" class="cursor-pointer"
          @click="$router.push({name: 'detail-career', params: {id: item.id}})"
          data-aos="fade-up"
          :data-aos-delay="200*i">
          <div class="wrapper">
            <div 
              class="content-img" 
              :style="{'background-image': `url('http://api.badranayalaw.credeva.id/uploads/picture/${item.image}')`}">
            </div>
          </div>
          <div class="title">
            {{ item.judul }}
          </div>
        </div>
      </div>
      <div @click="result=data" class="text-center">
        <button class="btn btn-home btn-lg">
          view more
        </button>
      </div>
    </div>
    <!--  -->
    <!-- <div class="container py-20">
      <Subscribe/>
    </div> -->
  </section>
</template>

<script>
// import Subscribe from '../Subscribe'
import http from '@/config/http'
export default {
  // components: {
  //   Subscribe
  // },
  data: () => ({
    data: [],
    result: []
  }),
  methods: {
    getData: function () {
      http.get('career')
        .then(res => {
          this.data = res.data.data
          this.result = this.data.slice(0, 3)
        })
    },
  },
  created() {
    this.getData()
  }
}
</script>

<style scoped>
.wrapper{
  max-height: 430px;
  @apply overflow-hidden;
}
.content-img{
  height: 430px;
  background-size: cover;
  transition-timing-function: ease-in-out;
  transform: scale(1);
  @apply w-full overflow-hidden bg-center;
  @apply duration-1000;
}
.content-img:hover{
  transform: scale(1.2);
}
.title{
  @apply text-2xl mt-4 text-center;
}
</style>