<template>
  <div>
    <section class="banner">
      <h1 :class="{'opacity-0': transparent}">CAREERS</h1>
    </section>
    <div class="bg-gray-200 py-20">
      <p class="w-full md:w-1/2 text-primary mx-auto text-center text-xl md:text-3xl px-4">
        Badranaya Partnership recruits the best talent available, 
        and is always looking to find and retain lawyers and graduates 
        with the skills, values, confidence and capabilities required to 
        deliver professional and service excellence.
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    transparent: false
  }),
  methods: {
    handleScroll() {
      if (window.scrollY > 330) {
        this.transparent = true
      } else {
        this.transparent = false        
      }
    }
  },  
  mounted () {
    window.addEventListener('scroll', this.handleScroll);
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
  }
}
</script>

<style scoped>
.banner{
  height: 90vh;
  background-image: url('../../assets/img/careers.jpg');
  background-position: center;
  @apply w-screen bg-fixed bg-cover flex items-end p-20;
}
h1{
  @apply text-white text-3xl tracking-wide leading-none opacity-100 duration-200;
}
@screen sm{
  h1{
    @apply text-6xl;
  }
}
</style>